.search {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.search .search_form {
 *
  zoom: 1;
  margin-top: 45px;
  background-color: #fff;
  padding: 12px 8px;
  position: fixed;
  z-index: 99;
  width: 100%;
}
*
.search .search_form::after {
  content: '';
  display: block;
  clear: both;
}
.search .search_form input {
  height: 35px;
  padding: 0 4px;
  border-radius: 2px;
  font-weight: bold;
  outline: none;
}
.search .search_form input.search_input {
  float: left;
  width: 76%;
  border: 4px solid #f2f2f2;
  font-size: 14px;
  color: #333;
  background-color: #f2f2f2;
}
.search .search_form input.search_submit {
  margin-left: 10px;
  width: 18%;
  border: 4px solid #009aff;
  font-size: 16px;
  color: #fff;
  background-color: #009aff;
}
.search .list {
  position: relative;
}
.search .list .list_container {
  position: absolute;
  top: 15%;
  width: 100%;
  background-color: #fff;
}
.search .list .list_container .list_li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 10px;
  border-bottom: 1px solid #e4e4e4;
}
.search .list .list_container .list_li .item_left {
  margin-right: 10px;
}
.search .list .list_container .list_li .item_left .restaurant_img {
  width: 50px;
  height: 50px;
  display: block;
}
.search .list .list_container .list_li .item_right {
  font-size: 12px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.search .list .list_container .list_li .item_right .item_right_text p {
  line-height: 12px;
  margin-bottom: 6px;
}
.search .list .list_container .list_li .item_right .item_right_text p:last-child {
  margin-bottom: 0;
}
.search .search_none {
  margin: 0 auto;
  color: #333;
  text-align: center;
  margin-top: 0.125rem;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}


